















































































import { Vue } from 'vue-property-decorator';
import UsersApi, { UserApiSearchAsyncParams } from '@/apiResources/UsersApi';
import { User } from '@/models/User';
export default Vue.extend({
    $_veeValidate: {
        validator: 'new'
    },
    components: {},
    data() {
        return {
            isShow: false,
            isUpdate: false,
            users: {} as User,
            loading: false,
            loadingSave: false,
            searchParamsUsers: {} as UserApiSearchAsyncParams,
            active: [] as any[],
            open: [],
            usersID: 0,
        }
    },
    watch: {
    },
    methods: {
        hide() {
            this.isShow = false
        },
        show(isUpdate: boolean, item: any) {
            this.$validator.reset();
            this.loadingSave = false;
            this.isShow = true;
            this.isUpdate = isUpdate;
            if (isUpdate) {
                this.usersID = item.UserId;
                this.getDataFromApi(this.usersID);
            }
            else {
                this.users = {} as User;
            }
        },
        getDataFromApi(id: number): void {
            UsersApi.deleteAsync(id).then(res => {
                this.users = res;
                this.users.Password = '';
            });
        },
        save(): void {
            this.$validator.validateAll('frmAddEdit').then((res) => {
                if (res) {
                    if (this.isUpdate) {
                        this.loading = true;
                        UsersApi.updateAsync(this.usersID, this.users).then(res => {
                            this.loading = false;
                            this.$emit("save");
                            this.isShow = false;
                            this.$snotify.success('Cập nhật thành công!');
                        }).catch(res => {
                            this.loading = false;
                            this.$snotify.error('Cập nhật thất bại!');
                        });
                    } else {
                        this.loading = true;
                        UsersApi.insertAsync(this.users).then(res => {
                            this.users = res;
                            this.users.Password = '';
                            this.isShow = false;
                            this.$emit("save");
                            this.loading = false;
                            this.$snotify.success('Thêm mới thành công!');
                        }).catch(res => {
                            this.loading = false;
                            this.$snotify.error('Thêm mới thất bại!');
                        });
                    }
                }
            });
        },
    }
});
