





























import { Vue } from 'vue-property-decorator';
export default Vue.extend({
    props: {
        value: {
            type: [Date, String],
            default: null
        },
        label: {
            type: String,
            default: ''
        },
        clearable: {
            type: Boolean,
            default: false
        },
        format: {
            type: String,
            default: 'DD/MM/YYYY'
        },
        formatInput: {
            type: String,
            default: 'YYYY-MM-DD'
        },
        max: {
            type: [Date, String],
            default: null
        },
        min: {
            type: [Date, String],
            default: null
        },
        disabled: {
            type: Boolean,
            default: false
        },
        hideDetails: {
            type: Boolean,
            default: false
        },
        appendIcon: {
            type: String,
            default: 'event'
        },
        prependIcon: {
            type: String,
            default: null
        },
        errorMessages: {
            default: null
        }
    },
    data() {
        return {
            mask: '##/##/####',
            menuDate: false,
            menuTime: false,
            selectedDate: null,
            valueMax: null,
            valueMin: null,
            x: 0,
            y: 0,
        }
    },
    created() {

    },
    computed: {
        dateTime(): any {
            if (this.selectedDate != undefined && this.selectedDate != null) {
                console.log(this.$moment(this.selectedDate).format());
                return this.$moment(this.selectedDate).format('YYYY-MM-DD');
            }
            else {
                return null;
            }
        },
        dateString(): any {
            if (this.dateTime != null && this.dateTime != undefined)
                return this.$moment(this.dateTime).format('DD/MM/YYYY');
            return null;
        }
    },
    methods: {
        dateInput(val: any) {
            console.log(val);
            if (val.length == 10 && this.$moment(val, "DD/MM/YYYY").isValid()) {
                this.selectedDate = this.$moment(val, "DD/MM/YYYY").format('YYYY-MM-DD');
                if (this.max !== null && this.$moment(this.dateTime) > this.$moment(this.max)) {
                    this.$emit('input', this.dateTime);//this.selectedDate = this.$moment(this.max).format('YYYY-MM-DD');
                }
                else {
                    this.$emit('input', this.dateTime);
                }
            } else if (val.length === 0) {
                this.selectedDate = null;
                this.$emit('input', this.dateTime);
            }
        },
        showMenuDate() {
            this.menuDate = true;
        },
        showMenuTime() {
            this.menuTime = true;
        },
        saveDate(date: any) {
            this.$emit('input', this.dateTime)
            this.menuDate = false
        },
        saveTime(time: any) {
            this.$emit('input', this.dateTime)
            this.menuTime = false
        },
        inputDate(val: any) {
            this.$emit('input', this.dateTime)
            this.menuDate = false
        },
        inputTime(val: any) {
            this.$emit('input', this.dateTime)
        }
    },
    watch: {
        max(val) {
            this.valueMax = this.max === null ? null : this.$moment(this.max, this.formatInput).format("YYYY-MM-DD");
        },
        min(val) {
            this.valueMin = this.min === null ? null : this.$moment(this.min, this.formatInput).format("YYYY-MM-DD");
        },
        value(val) {
            if (val != null && val != undefined && val != this.dateTime) {
                var date = val;
                this.selectedDate = this.$moment(date).format("YYYY-MM-DD");
            } else if (val != this.dateTime) {
                this.selectedDate = null;
            }
        }
    },
    mounted() {
        let val = this.value;
        if (val != null && val != undefined && val != this.dateTime) {
            var date = val;
            this.selectedDate = this.$moment(date).format("YYYY-MM-DD");
        } else if (val != this.dateTime) {
            this.selectedDate = null;
        }
    }
})
